import React, { useState, useEffect, useRef } from 'react';
import { auth } from '../firebase';
import { Link, useNavigate } from 'react-router-dom';

function Navbar({ onLoginButtonClick }) {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [displayName, setDisplayName] = useState('');
  const [userEmail, setUserEmail] = useState('');
  const [isNavCollapsed, setIsNavCollapsed] = useState(true);
  const [isCheckingAuth, setIsCheckingAuth] = useState(true);
  const [key, setKey] = useState(0);
  const navbarRef = useRef(null);
  const dropdownRef = useRef(null);
  const navigate = useNavigate();

  const handleLogout = () => {
    auth
      .signOut()
      .then(() => {
        console.log('Signed out successfully');
        navigate('/');
      })
      .catch((error) => {
        console.error('Error signing out:', error);
      });
  };

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      if (user) {
        setIsLoggedIn(user.emailVerified); // Set isLoggedIn based on email verification
        const userDisplayName = user.displayName;
        setDisplayName(userDisplayName ? userDisplayName : '');
        setUserEmail(user.email);
      } else {
        setIsLoggedIn(false);
        setDisplayName('');
        setUserEmail('');
      }
      setIsCheckingAuth(false);
      setKey((prevKey) => prevKey + 1);
    });

    return () => unsubscribe();
  }, [key]);
  useEffect(() => {
    function handleDocumentClick(event) {
      if (navbarRef.current && !navbarRef.current.contains(event.target)) {
        setIsNavCollapsed(true);
      }
    }

    document.addEventListener('click', handleDocumentClick);

    return () => {
      document.removeEventListener('click', handleDocumentClick);
    };
  }, []);

  const buttonMinWidth = Math.max(150, userEmail.length * 9);
  const buttonHeight = 42;

  const toggleNavbar = () => {
    setIsNavCollapsed(!isNavCollapsed);
  };

  const handleDropdownItemClick = () => {
    setIsNavCollapsed(true);
  };

  const isDesktop = window.innerWidth >= 1024;

  // Extract the first name from displayName
  const firstName = displayName.split(' ')[0];

  // Button text logic with spinner
  let buttonText;
  if (isCheckingAuth) {
    buttonText = (
      <div className="spinner-border " role="status">
        <span className="sr-only">Loading...</span>
      </div>
    );
  } else {
    buttonText = isLoggedIn ? firstName : 'Login';
  }

  return (

    <nav
    ref={navbarRef}
    className={`navbar navbar-expand-lg navbar-dark menu shadow fixed-top${isNavCollapsed ? '' : ' open'}`}
    style={{
      maxHeight: isNavCollapsed ? '80px' : '400px',
      overflow: isDesktop ? 'visible' : 'hidden',
      transition: 'max-height 0.7s ease-in-out',
    }}
  >

    
      <div className="container">
        <a className="navbar-brand" href="https://www.TioAssist.io/" aria-label="TioAssist Home">
        <img src={`${process.env.PUBLIC_URL}/images/logo/Logo.webp`} alt="TioAssist logo" width="180" />
        
        </a>
        <button className="navbar-toggler" type="button" onClick={toggleNavbar} aria-label="Toggle navigation menu">
  <span className="navbar-toggler-icon"></span>
</button>

        <div className={`collapse navbar-collapse justify-content-end${isNavCollapsed ? '' : ' show'}`} id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item"><a className="nav-link" href="/#Home">Home</a></li>
            <li className="nav-item"><a className="nav-link" href="/#Let's-Chat">Let's Chat!</a></li>
            <li className="nav-item"><a className="nav-link" href="/#Testimonials">Testimonials</a></li>
            <li className="nav-item"><a className="nav-link" href="/#FAQ">FAQ</a></li>
            <li className="nav-item"><a className="nav-link" href="/#Catch-Up">Catch-Up</a></li>
            <li className="nav-item"><a className="nav-link" href="/#Contact">Contact</a></li>
          </ul>
          <div className="dropdown" ref={dropdownRef}>
          <button
  className="rounded-pill btn-rounded login-button custom-dropdown-toggle"
  type="button"
  id="loginButtonNavDropdown"
  onClick={isLoggedIn ? toggleNavbar : onLoginButtonClick}
  style={{
    minWidth: `${buttonMinWidth}px`,
    height: `${buttonHeight}px`, // Add this line to set the fixed height
  }}
>
  {buttonText}
</button>
            {isLoggedIn && (
              <div className={`dropdown-menu${isNavCollapsed ? '' : ' show'}`} aria-labelledby="loginButtonNav">
                <Link to="/profile" className="dropdown-item" onClick={handleDropdownItemClick}>
                  Profile
                </Link>
                <a className="dropdown-item" href="#" onClick={handleDropdownItemClick}>
                  Settings
                </a>
                <a className="dropdown-item" href="#" onClick={handleDropdownItemClick}>
                  Chat
                </a>
                <div role="separator" className="dropdown-divider"></div>
                <a id="btnLogout" className="dropdown-item" onClick={handleLogout} href="#">
                  Logout
                </a>
              </div>
            )}
          </div>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;